html {
  box-sizing: border-box;
}

*, :before, :after {
  box-sizing: inherit;
  --primary-hue: 207;
  --secondary-hue: 0;
  --info-hue: 130;
  --error-hue: 5;
  --hue: var(--primary-hue);
}

@media screen and (width >= 600px) {
  .container {
    width: 80%;
    margin: auto;
  }

  body:has(.container) {
    margin-left: 0;
    margin-right: 0;
  }
}

.cover-page {
  position: relative;
}

.cover-page p {
  margin: 0;
}

.cover-page ul {
  margin: 0;
  padding-left: 1.5rem;
}

.cover-page .link-list {
  flex-wrap: wrap;
  align-items: flex-start;
  gap: .5rem;
  display: flex;
}

.head {
  justify-content: space-between;
  align-items: start;
  margin-bottom: .3rem;
  display: flex;
}

.head h1 {
  text-align: center;
  margin-top: 0;
}

.namebox {
  text-align: right;
  border: 1px dotted hsl(var(--hue), 100%, 40%);
  border-radius: .5rem;
  padding: .2rem .5rem;
}

.btn {
  --hue: var(--primary-hue);
  color: hsl(var(--hue), 100%, 33%);
  border: 1px solid hsl(var(--hue), 100%, 33%);
  user-select: none;
  text-align: center;
  border-radius: .5rem;
  padding: .3rem .5rem;
  text-decoration: none;
}

.btn:hover {
  color: hsl(var(--hue), 100%, 98%);
  background: hsl(var(--hue), 100%, 33%);
}

.btn:active {
  background: hsl(var(--hue), 100%, 25%);
  border-color: hsl(var(--hue), 100%, 25%);
}

.error-message {
  --hue: var(--error-hue);
  color: hsl(var(--hue), 100%, 27%);
  background-color: hsla(var(--error-hue), 100%, 90%);
  border-radius: 4px;
  align-items: center;
  margin: 4px 0 0;
  padding: 2px 4px;
  display: flex;
}

.error-message:hover {
  cursor: pointer;
}

.error-message:before {
  --hue: var(--error-hue);
  content: "✕";
  background-color: hsl(var(--hue), 100%, 90%);
  border: 1px solid hsl(var(--hue), 100%, 33%);
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  width: 1.2rem;
  height: 1.2rem;
  margin-right: 8px;
  display: flex;
}

.output {
  --hue: var(--info-hue);
  border: 1px dotted hsl(var(--hue), 100%, 50%);
  background-color: hsl(var(--hue), 100%, 95%);
  font-family: monospace;
  font-size: large;
}

.cols-wrap {
  flex-flow: wrap;
  justify-content: space-between;
  align-items: flex-start;
  gap: 1rem;
  display: flex;
}

.cols-wrap.two-cols > div {
  flex: 0 calc(50% - .5rem);
}

.rows-wrap {
  flex-flow: column wrap;
  justify-content: flex-start;
  gap: 1rem;
  display: flex;
}
